import React, { useEffect, useState } from 'react';
import Recipe from '../components/Recipe';
import Footer from '../components/Footer';

function App() {

    const appId = "06af91c6";
    const key = "5375cdbd60d73dfb59a1803bf548fa6e";
   
    const [recipes, setRecipes] = useState([]);
    const [find, setFind] = useState("");
    const [query, setQuery] = useState("Cocktail");

    useEffect(() => {
        getRecipes();
        // eslint-disable-next-line
    }, [query]);

    function updateFind(event) {
        setFind(event.target.value);
    }

    function handleSubmit(event) {
        setQuery(find);
        setFind("");
        event.preventDefault();
    }

    async function getRecipes() {
        const response = await fetch(`https://api.edamam.com/search?q=${query}&app_id=${appId}&app_key=${key}`);
        const data = await response.json();
        setRecipes(data.hits);
    }

    return (
        <div className="App">
            <h1 className="heading">Recipes</h1>
            <form onSubmit={handleSubmit} className="find-recipe">
                <input className="find-bar" onChange={updateFind} type="text" value={find} placeholder="search for your favorite recipe..." />
                <button className="find-button" type="submit">Search</button>
            </form>
            <div className="recipe-details">
                {recipes.map((item, index) => {
                    return(
                        <Recipe 
                            key={index} 
                            title={item.recipe.label}
                            image={item.recipe.image}
                            ingredients={item.recipe.ingredients} 
                        />
                    )  
                })}
            </div>
            <Footer />
        </div>
    )
}

export default App;